.calendar,
td,
tr {
    padding: 0;
    margin: 0;
}

.calendar {
    position: relative;
    background-color: white;
    border-spacing: 0;
    border-collapse: collapse;
    height: calc(50vh);
}

.day {
    cursor: pointer;
}

.day_all{
    margin: auto;
    line-height: 40px;
    display: block;
    width : 40px;
    height: 40px;
    border-radius: 50%;
    box-shadow: 0 0 5px #ff7f00;
    cursor: pointer;
}

.selected-day {
    background-color:#ff7f00;
    color: white;
}

.disabled-day{
    background: #ccc;
    cursor: no-drop;
    pointer-events: none;
    box-shadow: none;
}

.selected-day.day_all{
    box-shadow: none;
    font-weight: bold;
}

.month-popup {
    z-index: 1;
    top: 0;
    position: absolute;
    padding: 5px;
    background: white;
    border: 2px solid skyblue;
}

.label-month {
    cursor: pointer;
}

tbody tr td{
    width: 14%;
}

@media screen and (min-width: 320px) and (max-width: 760px) {
    .calendar-header{
      width: 100% !important
    }
  }
  
  .calendar-header{
    width: 75%
  }