body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.mt-10 {
  margin-top: 60px;
}

h1,h2,h3,h4,h5,h6,p,label{
  color: #45460c;
}

.nav-item.active a{
  color: #ff7f00 !important;
  border-bottom: 3px solid #8f931f;
}

a.link{
  color: #ff7f00 !important;
}

.carousel-height{
  height: calc(70vh);
}

.parallax {
  background-size: 100% 100%;
}

.parallax_physio {
  background-image: linear-gradient(rgba(69,70,12, 0.6), rgba(69,70,12, 0.6)), url("https://www.fitback.co.uk/wp-content/uploads/2015/02/fitback_banner_2-1900x700.jpg");
  min-height: calc(50vh) !important;
}

.parallax_contact {
  background-image: linear-gradient(rgba(69,70,12, 0.6), rgba(69,70,12, 0.6)), url("https://www.smg-marble.com/wp-content/uploads/2016/10/contactus-banner.jpg");
  min-height: calc(50vh) !important;
}

button:focus {
  outline: none !important;
}

.customBtn .MuiButton-label{
  color: #fff
}

@media screen and (min-width: 320px) and (max-width: 760px) {
  .carousel-height {
    height: calc(50vh);
  }
}

/* material verwrite css  */
.MuiStepIcon-root.MuiStepIcon-completed{
  color: #ff7f00 !important;
}
